.DDPanel-closed {
  position: absolute;
  height: 200px;
  width: 600px;
  border: solid black 7px;
  border-radius: 2rem;
  background-color: orange;
  top: 30px;
  right: 100px;
  pointer-events: none;
  transition: 0.2s ease-in-out;
  opacity: 0;
  display: flex;
  padding: 30px 30px;
  z-index: 10;
}

.DDPanel-open {
  position: absolute;
  height: 200px;
  width: 600px;
  border: solid black 7px;
  border-radius: 2rem;
  background-color: orange;
  top: 62px;
  right: 100px;
  pointer-events: auto;
  transition: 0.3s ease-in-out;
  opacity: 1;
  display: flex;
  padding: 30px 30px;
  z-index: 10;
}

.InnerPanel {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.PanelBtn {
  height: 60px;
  width: 250px;
  background-color: orange;
  border-radius: 2rem;
  border: 4px solid #000000;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  color: rgb(84, 14, 114);
  font-family: "Patrick Hand", cursive;
  font-size: 24px;
  padding: 16px 31px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #2f6627;
  transition: ease-out 0.3s;
}
.PanelBtn:hover {
  background-color: yellow;
}
.PanelBtn:active {
  position: relative;
  top: 1px;
}
